import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

import HomePage from '@/components/HomePage'
import NoFault from '@/components/NoFault'
import FailureRate from '@/components/FailureRate'
import TurnoutHealth from '@/components/TurnoutHealth'
import RealTimeAlarm from '@/components/RealTimeAlarm'

// 
import Alarm from '@/components/alarm/home'
import AlarmTopList from '@/components/alarm/AlarmTopListDetails'   //
import AlarmRatio from '@/components/alarm/AlarmRatioDetails'     //
import DeviceFault from '@/components/alarm/DeviceFaultDetails'   //
import SpeedRanking from '@/components/alarm/SpeedRankingDetails'    //
import OverrunRate from '@/components/alarm/OverrunRateDetails'     //

// 
import RemoteTour from '@/components/remotetour/home'
import RemoteTourChild from '@/components/remotetour/remotetour'
import CheckDetails from '@/components/remotetour/CheckDetails'

//
import ComprehensiveStatistics from '@/components/ComprehensiveStatistics/home'
import ComprehensiveStatisticsDetails from '@/components/ComprehensiveStatistics/ComprehensiveStatisticsDetails'    //
import Operator from '@/components/ComprehensiveStatistics/OperatorDetails'   //

//
import InfoList from '@/components/InfoList/home'
import NotRestored from '@/components/InfoList/NotRestored'   //
import untreated from '@/components/InfoList/untreated'     //


Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: '',
      component: HomePage
    },
    {
      path: '/remotetour',
      name: '',
      component: RemoteTour,
      children: [
        {
          path: '/remotetour/remotetour',
          name: '',
          hidden: false,
          component: RemoteTourChild,
        },
        {
          path: '/remotetour/checkdetails',
          name: '',
          hidden: false,
          component: CheckDetails
        }
      ], redirect: '/remotetour/remotetour'
    },
    {
      path: '/infolist',
      name: ' ',
      component: InfoList,
      children: [
        {
          path: '/infolist/untreated',
          name: '',
          hidden: false,
          component: untreated
        }, {
          path: '/infolist/notrestored',
          name: '',
          hidden: false,
          component: NotRestored
        }
      ], redirect: '/infolist/untreated'
    },
    {
      path: '/alarm',
      name: '',
      component: Alarm,
      children: [
        {
          path: '/alarm/AlarmTopListDetails',
          name: '',
          hidden: false,
          component: AlarmTopList
        },
        {
          path: '/alarm/alarmRatio',
          name: '',
          component: AlarmRatio
        },
        {
          path: '/alarm/DeviceFaultDetails',
          name: '',
          hidden: false,
          component: DeviceFault
        },
        {
          path: '/alarm/SpeedRanking',
          name: '',
          hidden: false,
          component: SpeedRanking
        },
        {
          path: '/alarm/overrunRate',
          name: '',
          component: OverrunRate
        },
      ], redirect: '/alarm/AlarmTopListDetails'
    },
    {
      path: '/nofault',
      name: '',
      component: NoFault
    },
    {
      path: '/failurerate',
      name: '',
      component: FailureRate
    },
    {
      path: '/ComprehensiveStatistics',
      name: '',
      component: ComprehensiveStatistics,
      children: [
        {
          path: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails',
          name: '',
          hidden: false,
          component: ComprehensiveStatisticsDetails
        },
        {
          path: '/ComprehensiveStatistics/operatorDetails',
          name: '',
          component: Operator
        },
      ], redirect: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails'
    },
    {
      path: '/TurnoutHealth',
      name: '',
      component: TurnoutHealth
    },
    {
      path: '/RealTimeAlarm',
      name: '',
      component: RealTimeAlarm
    }
  ],

  routes_RemoteTour: [
    {
      path: '/remotetour',
      name: '',
      component: RemoteTour,
      children: [
        {
          path: '/remotetour/remotetour',
          name: '',
          hidden: false,
          component: RemoteTourChild
        },
        {
          path: '/remotetour/checkdetails',
          name: '',
          hidden: true,
          component: CheckDetails
        }
      ], redirect: '/remotetour/remotetour'
    }
  ],
  routes_Alarm: [
    {
      path: '/alarm',
      name: '',
      component: Alarm,
      children: [
        {
          path: '/alarm/AlarmTopListDetails',
          name: '',
          hidden: false,
          component: AlarmTopList
        }, {
          path: '/alarm/alarmRatio',
          name: '',
          hidden: false,
          component: AlarmRatio
        },
        {
          path: '/alarm/DeviceFaultDetails',
          name: '',
          hidden: false,
          iconCls: 'el-icon-s-home',
          component: DeviceFault
        },
        {
          path: '/alarm/SpeedRanking',
          name: '',
          hidden: false,
          component: SpeedRanking
        },
        {
          path: '/alarm/overrunRate',
          name: '',
          hidden: false,
          component: OverrunRate
        },
      ], redirect: '/alarm/SpeedRanking'
    }
  ],
  routes_cs: [
    {
      path: '/ComprehensiveStatistics',
      name: '',
      component: ComprehensiveStatistics,
      children: [
        {
          path: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails',
          name: '',
          hidden: false,
          component: ComprehensiveStatisticsDetails
        },
        {
          path: '/ComprehensiveStatistics/operatorDetails',
          name: '',
          component: Operator
        }
      ], redirect: '/ComprehensiveStatistics/ComprehensiveStatisticsDetails'
    }
  ],
  routes_infolist: [
    {
      path: '/infolist',
      name: ' ',
      component: InfoList,
      children: [
        {
          path: '/infolist/untreated',
          name: '',
          hidden: false,
          component: untreated
        }, {
          path: '/infolist/notrestored',
          name: '',
          hidden: false,
          component: NotRestored
        }
      ], redirect: '/infolist/untreated'
    }
  ],
  // mode: 'history',
  srcollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  }
})



