     text-align: left;
    margin-top: 15px;
    font-size: 16px;
}
.divStyle > p:last-child {
  color: #acacac;
    font-size: 12px;
    margin-top: 0px;
}
.imageStyle {
  position: relative;
  transition: all 1s;
  cursor: pointer;
  padding: 6px;
  background: url(../../assets/photoFrame.png);
  background-size: 100%;
}
.imageStyle:hover {
  transform: scale(1.1);
}
.imageStyle p {
  position: absolute;
  right: 15px;
  bottom: 15px;
  color: #fff;
  background: #000;
  width: 49px;
  height: 24px;
  font-size: 14px;
  border-radius: 3px;
}
.photoFrame {
  position: absolute;
  left: -2px;
  top: -2px;
  width: 188px;
  height: 188px;
}
</style>



